<template>
  <div class="appAddress">
    <HeaderTab :isLeft="true" title2="选择收货地址"></HeaderTab>
    <div class="city_search">
      <div class="search">
        <span class="city" @click="$router.push('/city')">
          {{city}}
          <i class="fa fa-angle-down"></i>
        </span>
        <i class="fa fa-search"></i>
        <input type="text" v-model="search_val" placeholder="小区/写字楼/学校等">
      </div>
      <Location :address="address"/>
    </div>
    <!-- 搜索区域列表 -->
     <div class="area">
      <ul class="area_list" v-for="(item,index) in areaList" :key="index">
        <li @click="selectAddress(item)">
          <h4>{{item.name}}</h4>
          <p>{{item.district}}{{item.address.length>0?item.address:''}}</p>
        </li>
      </ul>
    </div>
  </div>
</template>


<script>
import HeaderTab from "../components/HeaderTab";
import Location from "../components/Location";

export default {
  name: "appAddress",
  components: {
    HeaderTab,
    Location
  },
  data() {
    return {
      search_val: "",
      city: "",
      address: this.$store.getters.location.formattedAddress,
      areaList:[]
    };
  },
  watch: {
    search_val() {
      this.searchPlace();
    }
  },
  methods: {
    selectAddress(item){
       this.$store.dispatch('setAddress',item.district + item.address + item.name);
       this.$router.push('/home')
    },
    searchPlace() {
      const that =this ;
      AMap.plugin("AMap.Autocomplete", function() {
        // 实例化Autocomplete
        var autoOptions = {
          //city 限定城市，默认全国
          city: that.city
        };
        var autoComplete = new AMap.Autocomplete(autoOptions);
        autoComplete.search(that.search_val, function(status, result) {
          // 搜索成功时，result即是对应的匹配数据
          // console.log()
          that.areaList = result.tips
        });
      });
    }
  },
  beforeRouteEnter(to, from, next) {
    // console.log(to);

    next(vm => {
      // console.log(vm)
      vm.city = to.params.city;
    });
  }
};
</script>


<style scoped>
.appAddress {
  width: 100%;
  height: 100%;
  overflow: auto;
  box-sizing: border-box;
  padding-top: 45px;
}

.city_search {
  background-color: #fff;
  padding: 10px 20px;
  color: #333;
}

.search {
  background-color: #eee;
  height: 40px;
  border-radius: 10px;
  box-sizing: border-box;
  line-height: 40px;
}
.search .city {
  padding: 0 10px;
}
.city i {
  margin-right: 10px;
}
.search input {
  margin-left: 5px;
  background-color: #eee;
  outline: none;
  border: none;
}

.area {
  margin-top: 16px;
  background: #fff;
}
.area li {
  border-bottom: 1px solid #eee;
  padding: 8px 16px;
  color: #aaa;
}
.area li h4 {
  font-weight: bold;
  color: #333;
  margin-bottom: 5px;
}
</style>
